<template>
  <div id="trash" class="detail">
    <div class="note-sidebar">
      <h3 class="notebook-title">回收站</h3>
      <div class="menu">
        <div>更新时间</div>
        <div>标题</div>
      </div>
      <ul class="notes">
        <li v-for="note in trashNotes">
          <router-link :to="`trash?noteId=${note.id}`">
            <span class="date">{{ note.updatedAtFriendly }}</span>
            <span class="title">{{ note.title }}</span>
          </router-link>
        </li>
      </ul>
    </div>
    <div class="note-detail">
      <div class="note-bar" v-if="currentTrashNote">
        <span>创建日期： {{currentTrashNote.createdAtFriendly}}</span>
        <span> | </span>
        <span>更新日期： {{currentTrashNote.updatedAtFriendly}}</span>
        <span> | </span>
        <span> 所属笔记本：{{belongTo}}</span>

        <a class="btn action" @click="onRevert">恢复</a>
        <a class="btn action" @click="onDelete">彻底删除</a>
      </div>
      <div class="note-title">
        <span>{{currentTrashNote.title}}</span>
      </div>
      <div class="editor">
        <div class="preview markdown-body" v-html="compiledMarkdown"></div>
      </div>
    </div>
  </div>
</template>

<script>
import Auth from '@/api/auth'
import MarkdownIt from 'markdown-it'
let md = new MarkdownIt()

export default {
  name: "TrashDetail",
  data(){
    return{
      msg: '回收站',
      currentTrashNote: {
        id: 3,
        title: '我的笔记',
        content: '## hello',
        createdAtFriendly: '2小时前',
        updatedAtFriendly: '刚刚'
      },
      belongTo: '我的笔记本',
      trashNotes: [
        {
          id: 3,
          title: '我的笔记',
          content: '## hello',
          createdAtFriendly: '2小时前',
          updatedAtFriendly: '刚刚'
        },
        {
          id: 3,
          title: '我的笔记',
          content: '## hello',
          createdAtFriendly: '2小时前',
          updatedAtFriendly: '刚刚'
        },
      ]
    }
  },
  created() {
    // 如果没有登录，点击回收站，自动跳转到登录页面
    Auth.getInfo().then(res => {
      if(!res.isLogin){
        this.$router.push({ path: '/login'})
      }
    })
  },
  computed: {
    compliedMarkdown(){
      return md.render(this.currentTrashNote.content || '')
    }
  },
  methods: {
    onRevert(){

    },
    onDelete(notebook) {
      console.log('delete')
    }
  },
}
</script>

<style lang="less" scoped>
@import "../assets/css/note-sidebar.less";
@import "../assets/css/note-detail.less";
#trash {
  display: flex;
  align-items: stretch;
  background-color: #fff;
  flex: 1;

  .note-bar {
    .action {
      float: right;
      margin-left: 10px;
      padding: 2px 4px;
      font-size: 12px;

    }
  }
}

</style>



