<template>
  <div id="note" class="detail">
    <NoteSideBar :curNote="currentNote" @update:notes="val => notes = val"></NoteSideBar>
    <!--    <h1>notebookId : {{$route.query.notebookId}}</h1>
        <h1>noteId : {{$route.query.noteId}}</h1>-->

    <div class="note-detail">
      <div class="note-empty" v-show="!currentNote.id">请选择笔记</div>
      <div class="note-detail-ct" v-show="currentNote.id">
        <div class="note-bar">
          <span>创建日期：{{ currentNote.createdAtFriendly }}</span>
          <span>更新日期：{{ currentNote.updatedAtFriendly }}</span>
          <span>{{ statusText }}</span>
          <span class="iconfont icon-delete" @click="onDeleteNote"></span>
          <span class="iconfont icon-fullscreen" @click="isShowPreview=!isShowPreview"></span>
        </div>
        <div class="note-title">
          <input type="text" v-model:value="currentNote.title" @input="onUpdateNote" @keypress="statusText='正在输入...'" placeholder="输入标题">
        </div>
        <div class="editor">
          <textarea v-show="isShowPreview" v-model:value="currentNote.content" @input="onUpdateNote" @keypress="statusText='正在输入...'" placeholder="输入内容, 支持 markdown"></textarea>
          <div class="preview markdown-body" v-html="previewContent" v-show="!isShowPreview"></div>
<!--          <div class="preview markdown-body" v-show="!isShowPreview"></div>-->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Auth from '@/api/auth'
import NoteSideBar from '@/components/NoteSideBar'

// 节流 npm 中的 lodash
import _ from 'lodash'

import MarkdownIt from 'markdown-it'
import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'


let md = new MarkdownIt()

export default {
  name: "NoteDetail",
  components: {
    NoteSideBar,
  },
  data() {
    return {
      statusText: '未改动',
      isShowPreview: true,
    }
  },
  created() {
    // 如果没有登录，点击笔记详情，自动跳转到登录页面
    this.checkLogin({path: '/login'})
  },
  computed: {
    ...mapGetters([
      'notes',
      'currentNote'
    ]),
    previewContent(){
      return md.render(this.currentNote.content || '')
    }
  },
  methods: {
    ...mapMutations([
      'setCurrentNote'
    ]),
    ...mapActions([
      'updateNote',
      'deleteNote',
      'checkLogin'
    ]),
    // _.debounce 等我输入停止300毫秒的时候才更新保存
    onUpdateNote: _.debounce(function(){
      console.log('111')
      this.updateNote({noteId: this.currentNote.id, title: this.currentNote.title, content: this.currentNote.content}).then(res => {
        this.statusText = '已保存'
      }).catch(err => {
        this.statusText = '保存出错'
      })
    },300),
    onDeleteNote(){
      this.deleteNote({noteId: this.currentNote.id}).then(res => {
        this.$router.replace({path: `/note`})
      })
    }
  },
  beforeRouteUpdate(to, from, next) {
    // console.log('beforeRouteUpdate')
    // console.log(to, from)
    this.setCurrentNote({currentNoteId: to.query.noteId})
    next()
  },
}
</script>

<style lang="less" scoped>
@import "../assets/css/note-detail.less";

#note {
  display: flex;
  align-items: stretch;
  background-color: #fff;
  flex: 1;
}
</style>



