<template>
  <div class="note-sidebar">
    <span class="btn add-note" @click="onAddNote">添加笔记</span>
    <el-dropdown class="notebook-title" @command="handleCommand" placement="bottom">
      <span class="el-dropdown-link">
        {{ currentBook.title }}<i class="iconfont icon-down"></i>
        <!--        <i class="iconfont icon-down"></i>-->
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item v-for="notebook in notebooks" :command="notebook.id">{{ notebook.title }}</el-dropdown-item>
        <el-dropdown-item command="trash">回收站</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <div class="menu">
      <div>更新时间</div>
      <div>标题</div>
    </div>
    <ul class="notes">
      <li v-for="note in notes">
        <router-link :to="`note?notebookId=${currentBook.id}&noteId=${note.id}`">
          <span class="date">{{ note.updatedAtFriendly }}</span>
          <span class="title">{{ note.title }}</span>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import Notebooks from '@/api/notebooks'
import Notes from '@/api/notes'
import Bus from '@/helpers/bus'
import {mapState, mapGetters, mapMutations, mapActions} from 'vuex'

// window.Notes = Notes

export default {
  name: "NodeSideBar",
  props: ['curNote'],
  data() {
    return {}
  },
  created() {
    this.getNotebooks() // 1. 得到所有的笔记本
      .then(()=>{
        // 2. 得到当前的笔记本
        this.setCurrentBook({currentBookId: this.$route.query.notebookId})
        // 3. 得到当前笔记本的所有笔记
        return this.getNotes({notebookId: this.currentBook.id})
      }).then(()=>{
        // 4. 得到当前选中的笔记
        this.setCurrentNote({currentNoteId: this.$route.query.noteId})
      })
    /*Notebooks.getAll()
      .then(res => {
        this.notebooks = res.data

        // 判断当前显示的笔记本
        this.currentBook = this.notebooks.find(notebook => notebook.id === parseInt(this.$route.query.notebookId))
          || this.notebooks[0] || {}
        // 更新 promise
        return Notes.getAll({notebookId: this.currentBook.id})
      }).then(res => {
      this.notes = res.data
      this.$emit('update:notes', this.notes)

      Bus.$emit('update:notes', this.notes)
    })*/


  },
  computed: {
    ...mapGetters([
      'notebooks',
      'notes',
      'currentBook',
    ]),
  },
  methods: {
    ...mapMutations([
      'setCurrentBook',
      'setCurrentNote'
    ]),
    ...mapActions([
      'getNotebooks',
      'getNotes',
      'addNote'
    ]),
    handleCommand(notebookId) {
      console.log("notebookId:", notebookId)
      if (notebookId === 'trash') {
        return this.$router.push({path: '/trash'})
      }

      // 在下拉栏下拉选择的时候，改变页面的上显示的笔记本
      // this.currentBook = this.notebooks.find(notebook => notebook.id === notebookId)
      this.$store.commit('setCurrentBook', {currentBookId: notebookId})


      // 实现下拉栏选择的时候，路径也变化
      this.$router.replace({path: `note?notebookId=${this.currentBook.id}`})

      this.getNotes({notebookId})
      /*Notes.getAll({notebookId})
        .then(res => {
          console.log("notes:", res)
          this.notes = res.data
          this.$emit('update:notes', this.notes)
        })*/
    },
    onAddNote() {
      this.addNote({notebookId: this.currentBook.id})
    }

  },

}
</script>

<style lang="less" scoped>

@import url(../assets/css/note-sidebar);

</style>




